import { Routes, RouterModule, PreloadAllModules } from '@angular/router';

import * as fromComponents from '@app/presentation/modules/active-user/components';

export const routes = [
  { path: '', component: fromComponents.ActiveUserComponent, pathMatch: 'full' }
];

export const ActiveUserRouting = RouterModule.forChild(routes);



