import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

import { Store, select } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';

import * as fromServicesShared from '@shared/services';
import * as fromServices from '@app/presentation/modules/active-user/services';

import * as fromStoreProfile from '@profile/store';
import * as fromStore from '@app/presentation/modules/active-user/store';

@Component({
  selector: 'app-good-job-active-user',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './active-user.component.html',
  styleUrls: ['./active-user.component.scss'],
  providers: [fromServices.ActiveUserService],
})
export class ActiveUserComponent implements OnInit {
  isLoading$: Observable<boolean>;
  loggedIn$: Observable<boolean>;

  public router: Router;

  public activeUserFormGroup: FormGroup;

  constructor(
    private _store: Store<fromStore.ActiveUserState>,
    private _formBuilder: FormBuilder,
    private _activeRoute: ActivatedRoute
  ) {
    this.isLoading$ = this._store.pipe(select(fromStore.getLoading));

    this.activeUserFormGroup = this._formBuilder.group({
      email: ['', Validators.required],
    });

    this.activeUserFormGroup = this._formBuilder.group({
      _id: ['', ''],
      password: ['', Validators.required],
      confirmPassword: ['', Validators.required],
      state: ['ACTIVE', '']
    }, {
      validator: fromServicesShared.ConfirmPassword.validate.bind(this)
    });
  }

  ngOnInit() {
    const routeParams = this._activeRoute.snapshot.queryParams;
    if (routeParams.user) {
      this.activeUserFormGroup.patchValue({
        _id: routeParams.user
      });
    }
  }

  onSubmit() {
    if (this.activeUserFormGroup.dirty && this.activeUserFormGroup.valid) {
      this._store.dispatch(new fromStore.ActivingUser(this.activeUserFormGroup.value));
    }
  }
}



